<template>
<div v-on:keyup.left="previousVerse" v-on:keyup.right="nextVerse" tabindex=-1>
  <v-container fluid class="mycontainer">
    <v-layout row wrap v-if="!isLoadingVerses">
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
          <v-slide-x-transition>
            <v-card v-show="show">
                <v-card-title>
                    <div>
                        <span v-if="isVerseOfDay" >||Shloka of the day||<br></span>
                        <span class="primaryText" v-html="verseText"></span> <br><br>
                        <span class="secondaryText" v-html="sanskritVerseText"></span>
                    </div>
                </v-card-title>
                <v-card-actions>
                  <v-layout>
                    <v-flex xs4>
                      <v-btn flat @click="previousVerse()"><v-icon>arrow_back</v-icon>Back</v-btn>
                    </v-flex>
                    <v-flex xs4>
                      <v-btn icon @click="toggleVerseLike(currentVerseId, isLike)">
                        <v-icon v-if="isLike" color="pink">favorite</v-icon>
                        <v-icon v-else disabled>favorite</v-icon>
                      </v-btn>
                    </v-flex>
                    <v-flex xs4>
                      <v-btn v-if="isVerseOfDay" flat @click="resumeReading()"><v-icon>settings_backup_restore</v-icon>Resume</v-btn><br/>
                    </v-flex>
                  </v-layout>
                </v-card-actions>
            </v-card>
          </v-slide-x-transition>
        </v-flex>
        <v-flex>
          <v-btn class="fixedButton" color="error" dark medium fab @click="nextVerse()">
                <v-icon>arrow_forward</v-icon>
            </v-btn>
        </v-flex>
    </v-layout>

    <v-layout row wrap align-center justify-center>
        <v-flex xs12 sm6 offset-sm3 class="text-xs-center">
          <v-dialog v-model="showProgress" align-center justify-center max-width="40px">
            <v-progress-circular indeterminate color="red"></v-progress-circular>
          </v-dialog>
        </v-flex>
    </v-layout>

    <v-layout row v-if="!isLoading">
      <v-flex xs12>
        <div style="padding-bottom:100px; padding-top:20px">
          <div v-for="comment in verseComments" :key="comment.id" style="padding:10px">
            <v-layout>
              <v-flex xs2>
                <v-avatar><img :src="comment.owner.image" alt=""/></v-avatar>
              </v-flex>
              <v-flex xs10>
                <h4>{{comment.owner.name}}</h4>
                <div>{{comment.commentText}}</div>
              </v-flex>
            </v-layout>
            <v-layout>
              <v-flex offset-xs4 xs2>
                <v-btn icon @click="toggleVerseCommentLike(comment.id, comment.isLike)">
                  <v-icon v-if="comment.isLike" color="red">thumb_up</v-icon>
                  <v-icon v-else color="grey lighten-1">thumb_up</v-icon>
                </v-btn>
              </v-flex>
              <v-flex xs2>
                <v-btn icon @click="toggleVerseCommentDislike(comment.id, comment.isLike)">
                  <v-icon v-if="comment.isLike === false" color="black">thumb_down</v-icon>
                  <v-icon v-else color="grey lighten-1">thumb_down</v-icon>
                </v-btn>
              </v-flex>
              <v-flex xs2>
                <v-btn icon @click="replyCliecked(comment.id)">
                  <v-icon color="grey">reply</v-icon>
                </v-btn>
              </v-flex>
              <v-flex xs2>
                <v-btn v-if="comment.owner.id === loggedinUserId" icon @click="deleteVerseCommentAlert(comment.id)"><v-icon color="grey lighten-1">delete</v-icon></v-btn>
              </v-flex>
            </v-layout>
            <hr>
          </div>
        </div>
      </v-flex>
    </v-layout>

    <v-layout row>
      <v-flex xs12>
        <form @submit.prevent="submit" >
          <div class="commentTextbox">
            <v-text-field flat multi-line rows="1" single-line counter=300 auto-grow label="Comment" v-model="commentText"
            append-icon="send" :append-icon-cb="submitVerseComment"></v-text-field>
          </div>
        </form>
      </v-flex>
    </v-layout>

    <!--replies-->
    <template>
      <v-layout row justify-center>
        <v-flex xs12>
          <v-dialog v-model="replyTextbox" fullscreen persistent max-width="290" style="margin:50px; padding:20px">
          <v-card>
             <v-toolbar card dark color="primary">
               <v-spacer></v-spacer>
              <v-btn icon @click.native="replyTextbox = false" dark>
                <v-icon>close</v-icon>
              </v-btn>
             </v-toolbar>
            <div style="padding:20px">
              <v-text-field flat multi-line rows="1" single-line counter=300 auto-grow label="Reply" v-model="commentReplyText"
              append-icon="send" :append-icon-cb="submitVerseCommentReply"></v-text-field>
            </div>
            <v-layout row v-if="!isLoading">
                <v-flex xs12>
                  <div style="padding-bottom:100px; padding-top:20px">
                    <div v-for="comment in verseCommentReplies" :key="comment.id" style="padding:10px">
                      <v-layout>
                        <v-flex xs2>
                          <v-avatar><img :src="comment.owner.image" alt=""/></v-avatar>
                        </v-flex>
                        <v-flex xs10>
                          <h4>{{comment.owner.name}}</h4>
                          <div>{{comment.commentText}}</div>
                        </v-flex>
                      </v-layout>
                      <v-layout>
                        <v-flex offset-xs4 xs2>
                          <v-btn icon @click="toggleVerseCommentReplyLike(comment.id, comment.isLike)">
                            <v-icon v-if="comment.isLike" color="red">thumb_up</v-icon>
                            <v-icon v-else color="grey lighten-1">thumb_up</v-icon>
                          </v-btn>
                        </v-flex>
                        <v-flex xs2>
                          <v-btn icon @click="toggleVerseCommentReplyDislike(comment.id, comment.isLike)">
                            <v-icon v-if="comment.isLike === false" color="black">thumb_down</v-icon>
                            <v-icon v-else color="grey lighten-1">thumb_down</v-icon>
                          </v-btn>
                        </v-flex>
                        <v-flex xs2>
                          <v-btn v-if="comment.owner.id === loggedinUserId" icon @click="deleteVerseCommentReplyAlert(comment.id)"><v-icon color="grey lighten-1">delete</v-icon></v-btn>
                        </v-flex>
                      </v-layout>
                      <hr>
                    </div>
                  </div>
                </v-flex>
              </v-layout>
          </v-card>
        </v-dialog>
        </v-flex>
      </v-layout>
    </template>

    <template>
      <v-layout row justify-center>
        <v-dialog v-model="dialogDeleteComment" persistent max-width="290">
          <v-card>
            <v-card-title class="headline">Delete</v-card-title>
            <v-card-text>Do you want to delete the comment?</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="green darken-1" flat @click.native="dialogDeleteComment = false">No</v-btn>
              <v-btn color="green darken-1" flat @click.native="deleteVerseComment()">Yes</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
    </template>

    <template>
      <v-layout row justify-center>
        <v-dialog v-model="dialogDeleteCommentReply" persistent max-width="290">
          <v-card>
            <v-card-title class="headline">Delete</v-card-title>
            <v-card-text>Do you want to delete the comment?</v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="green darken-1" flat @click.native="dialogDeleteCommentReply = false">No</v-btn>
              <v-btn color="green darken-1" flat @click.native="deleteVerseCommentReply()">Yes</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
    </template>
  </v-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: true,
      commentText: null,
      commentReplyText: null,
      dialogDeleteComment: false,
      dialogDeleteCommentReply: false,
      commentIdToDelete: 0,
      replyTextbox: false,
      parentCommentId: null
    }
  },

  mounted () {
    var verseOfDayId = this.$route.query.id
    if (verseOfDayId) {
      this.$store.dispatch('changeIndexToVerseOfDay', {id: verseOfDayId})
    }
    this.$store.dispatch('fetchVerseComments')
  },

  computed: {
    currentVerseId () {
      return this.$store.getters.currentVerseId
    },
    isLike () {
      return this.$store.getters.verseByIndex.isLike
    },
    verseText () {
      return this.$store.getters.verseText
    },
    sanskritVerseText () {
      return this.$store.getters.sanskritVerseText
    },
    isLoading () {
      return this.$store.getters.isLoading
    },
    isSaving () {
      return this.$store.getters.isSaving
    },
    showProgress () {
      return this.isSaving || this.isLoading
    },
    isLoggedIn () {
      return this.$store.getters.isLoggedIn
    },
    isLoadingVerses () {
      return this.$store.getters.isLoadingVerses
    },
    verseOfDayId () {
      return this.$store.getters.verseOfDayId
    },

    isVerseOfDay () {
      return this.verseOfDayId === this.currentVerseId
    },

    verseComments () {
      return this.$store.getters.verseComments
    },

    verseCommentReplies () {
      return this.$store.getters.verseCommentReplies
    },

    loggedinUserId () {
      var userId = this.$store.getters.user ? Number(this.$store.getters.user.userInfo.id) : 0
      // console.log('in loggedinuserid', userId)
      return userId
    }
  },

  methods: {
    nextVerse () {
      this.$store.dispatch('incrementVerseIndex')
      this.$router.replace({query: {id: this.currentVerseId}})
      this.$store.dispatch('fetchVerseComments')
    },
    previousVerse () {
      this.$store.dispatch('decrementVerseIndex')
      this.$router.replace({query: {id: this.currentVerseId}})
      this.$store.dispatch('fetchVerseComments')
    },
    resumeReading () {
      this.$store.dispatch('resumeReading')
      this.$router.replace({query: {id: this.currentVerseId}})
      this.$store.dispatch('fetchVerseComments')
    },
    submitVerseComment () {
      console.log('in submit comment')
      this.$store.dispatch('saveVerseComment', {commentText: this.commentText}).then(r => {
        this.commentText = null
      })
    },
    deleteVerseCommentAlert (id) {
      this.commentIdToDelete = id
      this.dialogDeleteComment = true
    },
    deleteVerseCommentReplyAlert (id) {
      this.commentIdToDelete = id
      this.dialogDeleteCommentReply = true
    },
    deleteVerseComment () {
      console.log('in deleteversecomment method')
      this.dialogDeleteComment = false
      this.$store.dispatch('deleteVerseComment', {commentId: this.commentIdToDelete, isReply: false})
    },
    deleteVerseCommentReply () {
      console.log('in deleteversecommentreply method')
      this.dialogDeleteCommentReply = false
      this.$store.dispatch('deleteVerseComment', {commentId: this.commentIdToDelete, isReply: true})
    },
    toggleVerseLike (id, isLike) {
      console.log('in toggleverselike', id)
      this.$store.dispatch('toggleVerseLike', {verseId: id, isLike: isLike})
    },
    toggleVerseCommentLike (id, isLike) {
      this.$store.dispatch('toggleVerseCommentLike', {commentId: id, isLike: isLike})
    },
    toggleVerseCommentDislike (id, isLike) {
      console.log('toggleVerseCommentDislike', id, isLike)
      this.$store.dispatch('toggleVerseCommentDislike', {commentId: id, isLike: isLike})
    },
    toggleVerseCommentReplyLike (id, isLike) {
      this.$store.dispatch('toggleVerseCommentLike', {commentId: id, isLike: isLike, isReply: true})
    },
    toggleVerseCommentReplyDislike (id, isLike) {
      console.log('toggleVerseCommentDislike', id, isLike)
      this.$store.dispatch('toggleVerseCommentDislike', {commentId: id, isLike: isLike, isReply: true})
    },
    replyCliecked (parentCommentId) {
      console.log('clicked', parentCommentId)
      this.replyTextbox = true
      this.parentCommentId = parentCommentId
      this.$store.dispatch('fetchVerseCommentReplies', parentCommentId)
    },
    submitVerseCommentReply () {
      console.log('in submit reply', this.parentCommentId)
      this.$store.dispatch('saveVerseComment', {commentText: this.commentReplyText, parentCommentId: this.parentCommentId}).then(r => {
        this.commentReplyText = null
      })
    }
  }
}
</script>
<style scoped>
    .fixedButton {
        position: fixed;
        opacity: 0.4;
        bottom: 30%;
        right: 20px;
        z-index:100;
    }
    .commentTextbox {
      position:fixed;
      bottom:0%;
      width:95%;
      z-index:50;
      background-color:white;
    }
    .primaryText {
      font-family: 'Arya', sans-serif;
      font-size: 20px;
      color: #212121;
    }
    .secondaryText {
      font-family: 'Karma', serif;
      font-size: 20px;
      color: #757575;
    }
    .commentReplyTextbox {
      position:fixed;
      bottom:30%;
      width:95%;
      z-index:60;
      background-color:white;
    }

@media only screen and (max-width: 599px) {
    .mycontainer {
      padding: 5px !important;
    }
}
</style>



